import React, { useEffect, useState } from 'react';
import axios from 'axios';
import classes from "./Main.module.css";
import QRCode from 'qrcode.react'; //  QRCode

export default function Main() {
  const [data, setData] = useState({ token: "", sessionId: "" });
  const [ws, setWs] = useState(null); //    WebSocket 

  useEffect(() => {
    //    GET 
    const fetchData = async () => {
      try {
        const response = await axios.get('http://147.45.102.206:3001/api/homepage');
        //     
        setData(response.data);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    //   fetchData   
    fetchData();
  }, []); //    data.sessionId,   fetchData     

  useEffect(() => {
    //  WebSocket    
    if (data.sessionId) {
      const wsUrl = `ws://147.45.102.206:8080?sessionId=${data.sessionId}`;
      const wsConnection = new WebSocket(wsUrl);

      setWs(wsConnection); //  WebSocket   

      //   WebSocket 
      wsConnection.onopen = () => {
        console.log('WebSocket  ');
      };

      wsConnection.onmessage = (event) => {
        console.log(' :', event.data);
        //   
        const message = JSON.parse(event.data);

        console.log(message.message)
        if (message !== 'false') {
          //     'false',  
          window.location.href = `#/${message.message}/user/new/start`;
        } else {
          //    'false',  
          alert(' ');
        }
      };

      wsConnection.onclose = () => {
        console.log('WebSocket  ');
      };

      //    
      return () => {
        wsConnection.close();
      };
    }
  }, [data.sessionId]); //   sessionId,      sessionId

  const qrUrl = `https://t.me/AcademyStrategBot?start=${data.token}-${data.sessionId}`;
  return (
    <div className={classes.main}>
      <div className={classes.qr}>   QR-</div>
      {/*  QRCode    */}
      <QRCode value={qrUrl} />
      <div className={classes.link}>
        <a href={qrUrl} target="_blank">   </a>
      </div>

      {/*    */}
      <div>Token: {data.token}</div>
      <div>Session ID: {data.sessionId}</div>
    </div>
  );
}
