import { styled } from '@mui/system';
import Switch from '@mui/material/Switch';

export const MuiSwitchLarge = styled(Switch)(({ theme }) => ({
 width: '68px', //    
 height: '34px', //    
 ".MuiSwitch-thumb": {
    backgroundColor: "#005475",
    width: '16px', //    
    height: '16px', //    
  },
  ".MuiSwitch-track": {
    backgroundColor: "#D9D9D9",
    width: '25px', //    
    height: '10px', //    
  },
  "& .MuiSwitch-switchBase": {
    "&.Mui-checked": {
      "+ .MuiSwitch-track": {
        backgroundColor: "#D9D9D9",
        width: '25px', //    
        height: '10px', //    
      },
      ".MuiSwitch-thumb": {
        backgroundColor: "#005475",
        width: '16px', //    
        height: '16px', //    
      },
    },
  },

}));
